.digital-kit {
  .swiper-digital-kit {
    padding: 40px 36px;
    .swiper-wrapper {
      transition-duration: 200ms !important; }
    .swiper-slide {
      background-color: #eeeeee;
      @include tr;
      transform: scale(0.97);
      border-radius: 10px;
      box-shadow: 0 0 5px 0 rgba(0, 0, 0, 0.1);
      overflow: hidden;
      &-active {
        background-color: $white;
        transform: scale(1); } } }
  .swiper-scroll {
    background-color: #f5f5f5;
    padding: 20px 0;
    padding-left: 0;
    padding-right: 80px;
    border-top: 1px solid rgba(#626262, 0.5);
    .swiper-slide {
      background: none;
      transform: scale(1);
      border-radius: 0px;
      box-shadow: none; } }
  .swiper-pagination-fraction {
    color: transparent;
    top: 13px;
    bottom: auto;
    span {
      font-size: 18px;
      line-height: 1;
      color: #9e9e9e;
      padding: 0 10px;
      &.text {
        position: absolute;
        left: 50%;
        transform: translate(-50%); } } }
  .slide__product {
    &__logo {
      @include center;
      padding: 20px 0; }
    &__img {
      @include center;
      margin-bottom: 35px;
      img {
        max-height: 253px;
        height: 100%; } }
    &__title {
      position: relative;
      &:before {
        content: '';
        width: 60px;
        height: 5px;
        border-radius: 2.5px;
        background-color: #ec135c;
        position: absolute;
        top: -20px;
        left: 50%;
        transform: translate(-50%); }
      h2 {
        font-size: 24px;
        font-weight: 600;
        text-align: center;
        color: #131c2e; }
      p {
        font-size: 18px;
        text-align: center;
        color: #9e9e9e;
        margin-top: 10px;
        margin-bottom: 20px;
        b {
          color: #31cd64;
          font-weight: normal; } } }
    &__function {
      width: 100%;
      display: flex;
      justify-content: flex-end;
      &__img {
        height: 33px;
        @include center;
        margin-bottom: 18px;
        img {
          height: 100%; } }
      a {
        font-size: 18px;
        display: inline-flex;
        flex-direction: column;
        justify-content: space-between;
        align-items: center;
        color: #131c2e; } } } }
