.buy {
  padding-bottom: 120px;
  position: relative;
  .tabs {
    &__caption {
      overflow: hidden;
      overflow-x: scroll;
      height: 72px;
      background-color: #131c2e;
      ul {
        display: flex;
        width: 670px; }
      li {
        padding: 24px 0;
        height: 67px;
        font-size: 21px;
        font-weight: 600;
        font-style: normal;
        font-stretch: normal;
        line-height: 0.95;
        letter-spacing: normal;
        text-align: left;
        color: rgba(255, 255, 255, 0.56);
        text-transform: uppercase;
        position: relative;
        @include center;
        &:first-child {
          width: 290px; }
        &:last-child {
          width: 380px; }
        &:before {
          content: '';
          width: 100%;
          height: 4px;
          background: transparent;
          position: absolute;
          bottom: -5px;
          left: 0;
          z-index: 77; }
        &.active {
          color: $white;
          &:before {
            background: #f2015b; } } } }
    &__content {
      display: none;
      padding: 0 24px;
      &.active {
        display: block; } } }
  .buy__result {
    p {
      font-size: 21px;
      color: #131c2e;
      display: block;
      padding: 30px 0;
      text-align: center; } }
  &__selected {
    height: 92px;
    background-color: #131c2e;
    padding: 0 30px 0 50px;
    position: fixed;
    display: none;
    bottom: 0;
    left: 0;
    width: 100%;
    &__content {
      display: flex;
      justify-content: space-between;
      align-items: center;
      height: 100%; }
    i {
      font-size: 24px;
      color: #ffffff;
      margin-right: 17px; }
    b {
      font-size: 21px;
      font-weight: 600;
      line-height: 0.95;
      color: #ffffff;
      i {
        font-size: 16px;
        margin-right: 6px; } }
    a {
      font-size: 18px;
      font-weight: 600;
      color: #131c2e;
      text-transform: uppercase;
      padding: 15px 45px;
      border-radius: 24px;
      border: solid 1px #ffffff;
      background-color: #ffffff; } }
  &__card {
    border-radius: 10px;
    box-shadow: 0 0 3.5px 0 rgba(0, 0, 0, 0.24);
    background-color: #ffffff;
    padding: 30px 25px;
    margin-bottom: 10px;
    &__coins {
      display: flex;
      justify-content: space-between;
      align-items: center; }
    strong {
      font-size: 18px;
      font-weight: normal;
      line-height: 1.11;
      color: #131c2e;
      text-transform: uppercase;
      display: block;
      padding: 15px 0; }
    p {
      font-size: 18px;
      line-height: 1.11;
      color: #9e9e9e;
      margin-bottom: 25px; }
    b {
      font-size: 24px;
      font-weight: normal;
      color: #f2015b;
      display: block;
      padding: 20px 0;
      i {
        font-size: 18px;
        margin-right: 7px; } }
    a {
      border-radius: 24px;
      border: solid 1px #131c2e;
      background-color: #ffffff;
      padding: 15px 35px;
      font-size: 18px;
      font-weight: normal;
      line-height: 1;
      text-transform: uppercase;
      color: #131c2e; } } }
