.order__payment {
  border-radius: 10px;
  box-shadow: 0 0 3.5px 0 rgba(0, 0, 0, 0.24);
  background-color: #ffffff;
  padding: 30px 30px 40px;
  margin-bottom: 20px;
}

.order__payment h3 {
  font-size: 18px;
  font-weight: 600;
  color: #131c2e;
  text-transform: uppercase;
  margin-bottom: 40px;
}

.order__payment__radio {
  display: flex;
  margin-top: 30px;
}

.order__payment__radio__block input {
  display: none;
}

.order__payment__radio__block input + label {
  font-size: 19.5px;
  color: #9e9e9e;
  position: relative;
  padding-left: 40px;
  margin-right: 40px;
  transition: all 0.3s;
}

.order__payment__radio__block input + label:before {
  content: '';
  position: absolute;
  left: 0;
  width: 24px;
  height: 24px;
  border: solid 2px #9e9e9e;
  background-color: #ffffff;
  border-radius: 100px;
  transition: all 0.3s;
}

.order__payment__radio__block input:checked + label {
  color: #131c2e;
}

.order__payment__radio__block input:checked + label:before {
  border: solid 6px #000000;
}

.order .buy__selected {
  display: block !important;
}

.order.shopping-cart .product-card__img {
  align-items: flex-start;
}

.order.shopping-cart .order__payment__radio {
  display: block;
}

.order.shopping-cart .order__payment__radio__block--input {
  position: relative;
  top: -15px;
}

.order.shopping-cart .order__payment__radio input + label {
  font-size: 18px;
  color: #131c2e;
  margin-bottom: 30px;
  display: flex;
  align-items: center;
  margin-right: 0;
}

.order.shopping-cart .order__payment__radio input + label input[type="text"] {
  display: block;
  font-size: 21px;
  line-height: 2.62;
  color: #9e9e9e;
  border-bottom: 1px solid #bdbdbd;
  width: 260px;
  margin-right: 20px;
}

.order.shopping-cart .order__payment__radio input + label input[type="text"]::placeholder {
  color: #9e9e9e;
}

.order.shopping-cart .order__payment__radio input + label input[type="submit"] {
  display: block;
  padding: 10px 30px;
  font-size: 18px;
  color: #131c2e;
  line-height: 1;
  border-radius: 18px;
  border: solid 1px #131c2e;
  background: none;
}

.order__pop-up {
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 430px;
  border-radius: 15px;
  box-shadow: 0px 1px 3px 0 rgba(0, 0, 0, 0.2);
  background-color: #ffffff;
  padding: 40px;
  padding-bottom: 120px;
  overflow: hidden;
}

.order__pop-up h2 {
  font-size: 24px;
  font-weight: 600;
  color: #131c2e;
  margin-bottom: 35px;
}

.order__pop-up li {
  font-size: 18px;
  line-height: 1.67;
  color: #616161;
}

.order__pop-up a {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 70px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 18px;
  color: #131c2e;
  background-color: #f5f5f5;
  text-transform: uppercase;
}
